import { Mutex } from 'async-mutex';

function nextImmediate() {
  return new Promise((ok) => setTimeout(ok, 100));
}

export class WebSocketPool {
  private connection?: WebSocket;
  openWaitingMutex!: Mutex;
  messageMutex!: Mutex;
  messageCallback!: (arg: any)=>void;

  constructor(url: string) {
    (async () => {
      this.connection = new WebSocket(url);
      this.openWaitingMutex = new Mutex();
      this.messageMutex = new Mutex();

      const release = await this.openWaitingMutex.acquire();

      this.connection.onerror = (ev) => {
        console.log(ev);
        // this.cleanup();
      }

      this.connection.onopen = () => {
        release();
      }

      this.connection.onmessage = ({ data }) => {
        // TODO
        if (this.messageCallback) {
          this.messageCallback(data);
        } else {
          throw new Error('orphan message received');
        }
      }

      this.connection.onclose = () => {
        // this.cleanup();
      }
    })();
  }

  close(): void {
    this.connection?.close();
  }

  response(message: any): Promise<any> {
    if (!this.connection)
      throw new Error('NYI');

    this.connection.send(JSON.stringify(message));
    return new Promise<any>(ok => {
      this.messageCallback = (message: any) => ok(JSON.parse(message));
    });
  }

  async send(message: any, abortOnFail = false): Promise<any> {
    let maxRetries = 6;
    let res;

    while (maxRetries--) {
      const openWaitingRelease = await this.openWaitingMutex.acquire();
      openWaitingRelease();
      const release = await this.messageMutex.acquire();
      try {
        res = await this.response(message);
        if (abortOnFail) return res;
        if (res.success) return res.data;
      } finally {
        release();
      }
      await nextImmediate();
    }

    throw new Error(res.message);
  }
}

export class WebSocketPoolV2 {
  private connection!: WebSocket;
  private promiseMap!: Map<string, (a: any) => void>;

  constructor(url: string) {
    this.promiseMap = new Map();

    (async () => {
      this.connection = new WebSocket(url);

      this.connection.onerror = (ev) => {
        console.log(ev);
        // this.cleanup();
      }

      this.connection.onmessage = ({ data }) => {
        this.processIncomingMessage(data);
      }
    })();
  }

  ready(): Promise<void> {
    return new Promise(ok => {
      if (this.connection.readyState !== WebSocket.OPEN) {
        setTimeout(() => this.ready().then(ok), 1)
      } else if (this.connection.readyState !== WebSocket.CLOSED) {
        ok();
      } else {
        throw new Error('WebSocket is closed');
      }
    });
  }

  async send(message: any, abortOnFail = false): Promise<any> {
    await this.ready();

    const executor = (message: any): any => (new Promise(async (ok, err) => {
      const param = JSON.stringify(message);

      while (this.promiseMap.has(param)) {
        await nextImmediate();
      }

      this.promiseMap.set(param, ok);
      this.connection.send(param);
    }));

    let maxRetries = 6;
    let res;

    while (maxRetries--) {
      try {
        res = await executor(message);
        if (abortOnFail) return res;
        if (res.success) return res.data;
      } catch (e) {
        console.error('send():', e);
      }

      await nextImmediate();
    }

    throw new Error(res.message);
  }

  private processIncomingMessage(_message: string) {
    const message = JSON.parse(_message);
    const param = JSON.stringify(message.parameter);

    if (!this.promiseMap.has(param)) {
      console.log('Unknown RPC response from request param', JSON.parse(param));
    } else {
      this.promiseMap.get(param)?.(message);
      this.promiseMap.delete(param);
    }
  }
}
