






import Vue from 'vue';
import { getStatus } from '@/api/api';
import { sleep } from '@/lib/utils';

export default Vue.extend({
  name: 'Home',
  data() {
    return {
      sessions: -1,
    };
  },
  async mounted() {
    while (true) {
      this.sessions = (await getStatus()).session_count;

      await sleep(30000);
    }
  }
})
