<template>
  <header>
    <h1 data-content="Hertz Electric">Hertz Electric</h1>
    <span>Vintage but modern, all manually automated.</span>
    <div class="nav">
      <div class="left-content">
        &nbsp;&nbsp;
        <router-link :to="{ name: 'Home' }">Home</router-link>
        &nbsp;&nbsp;|&nbsp;&nbsp;
        <a href="/about.txt">About</a>
      </div>

      <form class="right-content" @submit="goSearch($event)">
        <input v-model="search" placeholder="Search (ASN, resource ID)" />
        &nbsp;
        <button @click="goSearch" type="submit">Go!</button>
        &nbsp;
        <button @click="goHelp" type="button">?</button>
        &nbsp;&nbsp;
      </form>
    </div>
  </header>
</template>

<script>
export default {
  name: "HzHeader",
  data() {
    return {
      search: '',
    }
  },
  methods: {
    goSearch(e) {
      if (e) e.preventDefault();

      const search = this.search;

      if (!isNaN(+search) || /^as(\d+)$/i.test(search)) {
        this.$router.push({
          name: 'SearchAsn',
          query: {
            asn: search.replace(/[a-zA-Z]/mg, '')
          }
        });
      } else {
        console.log('search', search);
        this.$router.push({
          name: 'Detail',
          query: {
            type: 'output',
            rid: search,
          }
        });
      }
    },
    goHelp() {
      alert('For ASxxxxx: xxxxx or directly search for a resource ID (e.g. P6XXXXXXXDYYYEL).');
    }
  }
}
</script>

