









































import Vue from 'vue';
import SmartTable from '@/components/SmartTable.vue';
import { getResource, getStatus, getUser, listResources } from '@/api/api';
import { toHumanDateTime } from '@/lib/utils'; // @ is an alias to /src

export default Vue.extend({
  name: 'SearchAsn',
  components: { SmartTable },
  data() {
    return {
      refreshing: true,
      asn: 0,
      timer: 0,
      resources: [] as any[],
      userStats: [] as any[],
      userStatsSchema: [
        {
          prop: 'status',
          name: 'status',
        },
        {
          prop: 'as_set_source',
          name: 'AS SET source',
        },
        {
          prop: 'as_set',
          name: 'as-set'
        }
      ],
      resourcesSchema: [
        {
          prop: 'resource_id',
          name: 'resource'
        },
        {
          prop: 'nei_ip',
          name: 'ip',
        },
        {
          prop: 'reporter',
          name: 'router'
        },
        {
          prop: 'status',
          name: 'state'
        },
        {
          prop: 'imported',
          name: 'accepted'
        },
        {
          name: 'filtered',
        },
        {
          name: 'log',
        },
        {
          prop: '_updated',
          name: 'updated'
        }
      ]
    }
  },
  created() {
    this.asn = +this.$route.query.asn || 135395;
  },
  mounted() {
    // query
    if (this.timer) clearInterval(this.timer);

    this.timer = setTimeout(() => this.updateData(), 30*1000);
    this.updateData();
  },
  beforeDestroy() {
    // clear interval
    clearTimeout(this.timer);
  },
  methods: {
    async updateData() {
      this.refreshing = true;
      const [userStats, resources] = await Promise.all([getUser(this.asn), listResources(this.asn)]);
      this.userStats = [userStats] as any;

      // placeholder
      if (!this.resources.length)
        this.resources = resources.map(rid => ({ resource_id: rid, _failed: false }));

      // fetch all resource stats
      for (const res of await Promise.all(resources.map(id => getResource(id.toString(), true)))) {
        let resourceStat: any;

        if (!res.success) {
          resourceStat = {
            resource_id: res.parameter.resource_id,
            _failed: true,
            _message: res.message
          };
        } else {
          resourceStat = Object.assign({ _failed: false }, res.data);
        }

        resourceStat = Object.assign({}, resourceStat, { _updated: toHumanDateTime(Date.now()/1000) })

        const idx = this.resources.findIndex(r => r.resource_id === resourceStat.resource_id) ?? -1;
        // console.log('find idx for', resourceId, idx, this.resources);
        if (idx > -1) {
          this.resources.splice(idx, 1, resourceStat);
        } else {
          this.resources.push(resourceStat);
        }
      }

      this.refreshing = false;
      this.timer = setTimeout(() => this.updateData(), 10*1000);
    },
  }
})
