<template>
  <div class="wrapper">
    <div>See also:&nbsp;&nbsp;
    <router-link
      :is="detailType === 'output' ? 'span' : 'router-link'"
      :to="{ name: 'Detail', query: { rid: resourceId, type: 'output' }}"
    >Protocol status</router-link>&nbsp;|&nbsp;
    <router-link
      :is="detailType === 'filtered' ? 'span' : 'router-link'"
      :to="{ name: 'Detail', query: { rid: resourceId, type: 'filtered' }}"
    >Filtered</router-link>&nbsp;|&nbsp;
    <router-link
      :is="detailType === 'accepted' ? 'span' : 'router-link'"
      :to="{ name: 'Detail', query: { rid: resourceId, type: 'accepted' }}"
    >Accepted</router-link>
    </div>
    <p>Route details ({{ detailType }}) for {{ resourceId }}:<br>
      Updated: {{ updated }}. Update every 30 seconds.</p>
    <br>
    <pre>{{ result }}</pre>
  </div>
</template>

<script>
import Vue from 'vue';
import { getDetail } from "@/api/api";
import { toHumanDateTime } from "@/lib/utils";

export default Vue.extend({
  name: 'ResourceDetail',
  data() {
    return {
      detailType: '?',
      resourceId: '',
      result: '',
      updated: '',
      timer: 0,
    }
  },
  mounted() {
    this.detailType = this.$route.query.type;
    this.resourceId = this.$route.query.rid;
    this.timer = setInterval(() => this.query(), 30*1000);
    this.query();
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  methods: {
    async query() {
      this.updated = '(refreshing)';
      try {
        const result = await getDetail(this.resourceId, this.detailType);
        this.result = result;
        this.updated = toHumanDateTime(Date.now() / 1000);
      } catch (e) {
        this.result = 'Failed to get details: ' + e.message;
        this.updated = toHumanDateTime(Date.now() / 1000);
      }
    }
  }
});
</script>

