import { wsPool } from '@/lib/globals';

export async function getStatus() {
  return await wsPool.send({
    service: 'routing',
    action: 'status'
  });
}

export function listResources(asn: number): Promise<number[]> {
  return wsPool.send({
    service: 'routing',
    action: 'list_resources',
    asn
  });
}

export function getResource(resourceId: string, abortOnFail = false) {
  return wsPool.send({
    service: 'routing',
    action: 'get_resource',
    resource_id: resourceId
  }, abortOnFail);
}

export function getDetail(resourceId: string, type: string) {
  return wsPool.send({
    service: 'routing',
    action: 'get_detail',
    resource_id: resourceId,
    type
  });
}

export function getUser(asn: number) {
  return wsPool.send({
    service: 'routing',
    action: 'get_user',
    asn
  });
}
